.viewJob {
   display: flex;
   justify-content: center;
   margin-left: auto;
   width: 100%;
   flex-direction: column;
}

.viewJob__banner {
   object-fit: cover;
   width: 100%;
   height: 100%;
   position: fixed;
   z-index: -1;
}

.viewJob__header {
   height: 40vh;
   width: 100%;
   justify-content: center;
   box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0);
   flex-direction: column;
   flex-direction: center;
}

.viewJob__header > h1 {
   color: #fff;
   font-size: 70px;
   margin-top: -100px;
   font-family: 'Montserrat', sans-serif;
   padding-top: 12.5%;
   text-align: center;
   vertical-align: middle;
}

.viewJob__row {
   flex-direction: center;
   margin-left: auto;
   margin-right: auto;
   margin-top: 1%;
   padding-top: 2%;
   padding-bottom: 30px;
   padding-left: auto;
   padding-right: auto;

   display: grid;
   grid-template-columns: repeat(4, minmax(250px, 1fr));
}

.viewJob__cardItem {
   padding-bottom: 30px;
}

.viewJob__filters {
   height: 100px;
   background-color: rgb(240, 240, 240);
   align-items: center;
   padding-left: auto;
   padding-right: auto;
   display: flex;
}

.viewJob__filters > h1 {
   justify-content: center;
   padding-right: 10px;
}

.viewJob__container {
   background: white;
}

.viewJob__filters > Button {
   font-size: 30px;
}
.viewJob__filters > {
   font-size: 30px;
}

.viewJob > Footer {
   padding-top: 2%;
}
