.btn {
   padding: 8px 20px;
   border-radius: 2px;
   outline: none;
   border: none;
   cursor: pointer;
}

.btn-hover {
   background-image: linear-gradient(
      to right,
      #9733ee,
      #da22ff,
      #da22ff,
      #9733ee
   );
   box-shadow: 0 3px 10px 0 #190030;
   width: 200px;
   font-size: 16px;
   font-weight: 600;
   color: #fff;
   cursor: pointer;
   margin: 20px;
   height: 55px;
   text-align: center;
   border: none;
   background-size: 300% 100%;

   border-radius: 50px;
   transition: all 0.4s ease-in-out;
}

.btn-hover:hover {
   background-position: 100% 0;
   transition: all 0.4s ease-in-out;
}

.btn-hover:focus {
   outline: none;
}

.btn--outline {
   background-color: transparent;
   color: #fff;
   padding: 8px 20px;
   box-shadow: 0 3px 10px 0 #21003d;
   border: 2px solid var(--primary);
   border-radius: 50px;
   transition: all 0.3s ease-out;
}

.btn--small {
   padding: 6px 15px;
   font-size: 16px;
   width: 110px;
   height: 45px;
}

.btn--medium {
   padding: 8px 20px;
   font-size: 18px;
}

.btn--large {
   padding: 12px 26px;
   font-size: 20px;
}

.btn--outline:hover {
   transition: all 0.3s ease-out;
   background: #da22ff;
   color: #ffffff;
   transition: 250ms;
}

